import { ActionCellAPI, CollectionOptimisticActions } from '@wix/patterns';
import {
  UpdateActionConfig,
  CustomActionConfig,
  SchemaConfig,
  BulkDeleteActionConfig,
  DeleteActionConfig,
  CreateActionConfig,
  BaseSDK,
} from '../../types';
import { IconElement } from '@wix/design-system';

export interface ResolvedAction {
  label: string;
  icon: IconElement;
  onClick: () => void;
  disabled?: boolean;
  tooltip?: string;
  skin?: string;
}

export interface BaseContextParams {
  schema: SchemaConfig;
}

export interface ActionsSDK extends BaseSDK {
  getSchema: (collectionId: string) => SchemaConfig | undefined;
  collectionId: string; // TODO: Why collectionId is needed here? getSchema with internal collectionId is enough
  getOptimisticActions: (
    collectionId: string,
  ) => CollectionOptimisticActions<any, any>;
}

export interface BaseActionParams<T = Record<string, any>> {
  sdk: ActionsSDK;
  actionParams: T;
}

export interface ActionCellBaseParams extends BaseActionParams {
  actionParams: {
    item: any;
    index: number;
    api: ActionCellAPI<any, any>;
  };
}

export interface BulkDeleteActionParams extends BaseActionParams {
  action: BulkDeleteActionConfig;
  actionParams: {
    selectedValues: any[];
    total: number;
  };
}

export interface DeleteActionParams extends ActionCellBaseParams {
  action: DeleteActionConfig;
}

export interface UpdateActionParams extends ActionCellBaseParams {
  action: UpdateActionConfig;
}

export interface CreateActionParams extends BaseActionParams {
  action: CreateActionConfig;
}

export interface CustomActionParams<T> extends BaseActionParams<T> {
  action: CustomActionConfig;
}

export interface ActionModalProps {
  modalParams: Record<string, any>;
  actionParams: Record<string, any>;
  sdk: ActionsSDK;
}

export interface CustomActionModalProps extends ActionModalProps {
  modalParams: { id: string };
}
